$bg:rgb(245, 245, 245);
$sweep-blue: #00529E;
$blue:  #00529E;
$red:#E34747;
$green:#73C167;

html {	
	overflow-y: scroll;
}

.hidden {
	display:none;
}

#whitebg {
	z-index: -1;
	background-color: $bg;
	position: fixed;
	bottom:0;
	height: 100vh;
	width: 100%;
	margin:0;
	padding:0;
}
#admin-panel 
{
	background-color: $bg;
	padding-bottom:3%;
	font-family: "poppins", sans-serif;

	.admin-container {
		background-color: $bg;
		z-index: inherit;
		margin-bottom: auto;
	}
	.account-options {
		a {
			cursor: pointer;
		}
	}
	.red {
		color:red;
	}
	.green {
		color:green;
	}
	.overlay {		
		.overlay-buttons {
			display: flex;
			flex-direction: row;	
			margin: auto;
			justify-content: space-around;
			width: 100%;
		}
		.button {
			border-radius: 5px;
			padding:5% 0;	
			text-align: center;	
			margin: auto;
			width: 5vw;
			font-size: 1.3rem;
		}				
		.button-red {
			background-color: $red;
			color: white;
		}
		.button-green {
			color: white;
			background-color: $green;				
		}
	}

	.admin-header {
		background-color: white;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 5%;
		align-items: center;
		height: 10vh;

		.header-account {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			width: 10vw;

			img {
			margin-right: 20%;
			}

			.account-options {
				display: flex;
				flex-direction: column;
				align-items: flex-start;

				span {
					font-weight: bold;
				}
			}
		}

		h1 {
			font-size: 18px;
			font-family: "poppins",sans-serif;
			font-weight: normal;
		}

		img {
			height: 4vh;
		}

		.logo-icon {
			height: 7vh;
		}
	}

	.admin-filters {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		padding: 0 20%;
		padding-top: 3%;
		padding-bottom: 1%;
		align-items: center;
		
		.activefilter path{
			fill:$blue;
		}
		
		.admin-filter-views {
			display: flex;
			justify-content: space-between;
			flex-direction: row;

			a {
				padding-left: 50%;
			}
		}
		.admin-filter-options {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: flex-start;
			white-space: nowrap;
			margin-right: 3%;

			label {
				margin-right: 4%;
			}

			select {
				margin-left: 3%;
				padding: 3% 15%;
				border-radius: 5px;
				font-family: "poppins", sans-serif;
				font-size: 1rem;
			}
		}
	}

	.upper-title{
		text-align: center;
	}

	.status-msg {
		text-align: center;
	}
	
	.list-view {
		margin: auto;	
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		justify-content: space-around;
		width: 70vw;

		.item {
			margin-bottom:4%;
		}

		.item-details-info{
			margin: 3% auto;
			margin-left: 5%;
		}

		.item-details-cover {
			position: relative;
			height: 30%;

			.item-details-img {
				max-width: 30vw;
				max-height: 30vw;
				object-fit: fill;
			}					
		}
	}

	.block-view {	
		margin: auto;	
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-around;
		width: 90vw;

		.item {
			width: 40vw;
			margin: 4%;
		}

		.item-details {
			display: flex;	

			.item-details-info {
				margin: 3% auto;
				margin-left: 3%;
			}	
			
			.item-details-cover  {
				position: relative;
				
				.item-details-img {
					max-width: 20vw;
					display: block;
					max-height: 25vh;
					object-fit: fill;	
				}					
			}
		}
	}

	.item {
		box-shadow: 0 1px 3px 0 black, 0 2px 5px 0 rgba(0, 0, 0, 0.19);
		border-radius: 5px;		
		background-color: rgba(255, 255, 255, 1);
		margin: 1% 0;
		z-index: 1;

		.header-green{
			background-color: green;			
		}

		.header-red {
			background-color: red;			
		}

		.header-blue {
			background-color: $blue;
		}

		.item-details-buttons {
			display: flex;
			height: 100%;
			flex-direction: column;
			justify-content: center;
			margin-right: 2%;
		}

		.item-header {
			border-radius: 5px 5px 0 0;
			display: flex;
			justify-content: space-between;
			color: white;
			padding: 10px 20px;

			h3 {
				margin: 0;
			}
		}
		
		.item-details-info-sec-desc {
			word-wrap:break-word;
			width: 30vw;
		}
		
		.item-expand {
			margin:5%;
			z-index: 2;
			.item-details-info-mobile {
				display: none;
				margin: 15% 0;

				.small-icon {
					margin-right: 10px;
					width: 20px;
					height: 20px;
				}
		}

		.item-location {
			h3 {
				margin:0;
			}
			span {
				color:grey;
			}
			.map {
				margin: 2% 0;
			}
			.item-buttons{
				display: flex;
				flex-direction: row;
				justify-content: space-around;
				align-items: center;
				font-size: 18px;																
				margin: 5% auto;
				a {
					text-decoration: none;							
					white-space: nowrap;
				}
			}
			.button {
				border-radius: 5px;
				padding:5% 0;	
				text-align: center;	
				margin: auto;
				width: 15vw;	
			}
			
			.button-red {
				background-color: $red;
				color: white;
			}
			.button-green {
				color: white;
				background-color: $green;
			}
		}
		.item-shrink {
			width: 100%;
			padding-bottom: 3%;
			padding-top: 2%;
			text-align: center;
			border-top: 2px solid rgba(0, 0, 0, 0.19);
			cursor: pointer;
			margin-bottom: 20%;
		}
	}

		.item-details 
		{
			display: flex;			

			.item-details-cover {
				position: relative;
				
				.item-details-img 	{
					display: block;
					width: 30vw;
					object-fit: fill;
					height: 30vh;
				}							
				.item-details-buttons 
				{
					position:absolute;
					top:0px;
					right: 0px;
				}
			}
			.item-details-info {
				font-size: 16px;

				h3 {
					margin-bottom:10%;
				}
			}
			.small-icon {
				margin-right: 10px;
				width: 20px;
				height: 20px;
			}
		}			
	}

	.pagination {
		list-style: none;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		width: 60%;
		margin:auto;
		margin-top: 3%;
		margin-bottom: 8%;

		a {
			text-decoration: none;
			color:$blue;
			font-weight: bold;
			cursor: pointer;
			width: 50px;
			height: 50px;
			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				background-color: $blue;
				color: white;
			}
		}

		li {
			border: 3px solid $blue;
			border-radius: 5px;
			text-align: center;
			margin: 0 5%;
		}
	}

	.navbar {
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 3;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		background-color: white;
		height: 10%;
		
		.nav-item {
			display: flex;			
			align-items: center;
			flex-direction: column;
			justify-content: center;

			label {
				color:grey;
			} 
		}
		
		.activenav {
			background-color: white;
			border-radius: 10px;
			display: inline-flex;
		}

		.activenav svg path{
			fill:$blue;
		}	

		.activenav label {
			color:$blue;			
		}
	}
	
	.navbarbg {
		position: fixed;
		z-index: 3;
		bottom: 9%;
		width: 100%;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-around;
		margin:auto;

		.hidden img{
			display: none;
		}

		.nav-item {
			display: flex;
			align-items: center;
			flex-direction: column;
			width: 65px;
			background-color: none;

			&:nth-child(2) {
				padding: 0 0vw;
			}

			label {
				display: none;
			} 			
		}
		
		svg{
			display: none;
		}		
		label {
			display: none;			
		}
	}
	
	@media (min-width: 480px) {
		.item-details-info-mobile {
			display: hidden;
		}
	}
	@media (min-width: 320px) and (max-width: 530px)
	{		
		.back-button {
			border-radius: 5px;
			padding:1% ;	
			text-align: center;	
			margin: auto;
			margin-top: 5%;
			width: 77vw;
			padding: 5vw 0;
			border: 3px solid #00539F;
			color: #00539F;
			font-size: 18px;
			border-radius: 5px;
			cursor: pointer;
		}

		.overlay-component-accountoptions {
			background-color: rgba(255,255,255,0.9);
			width: 100%;
			h1 {
				text-transform: capitalize;
				text-align: center;
				font-size: 2.2rem;
			}			
		}

		#login-container {
			margin-top: 15%;

			img {
				display: none;
			}
		}

		.header-account{
			.butwhy{
				display: none;
					
			}
		}
		.overlay {
			.button {
				width: 30vw;
			}

			.overlay-accountoptions {
				display: flex;
				flex-direction: column;
				position: absolute;
				justify-content: space-around;
				right:30%;
				top:30%;
				
				a{
					font-size:2rem;
					color:black;
					margin:15% 0;
				}				
			}
		}
		
		.admin-filter-views a{
			display: none;
		}

		.list-view {
		margin: auto;	
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		justify-content: space-around;
		width: 90vw;

		.item {
			margin-bottom:4%;
		}
		
		.item-details-info{
			display: none;
		}

		.item-details-buttons {
			right:0;
		}

		.item-details-cover	{
			position: relative;
			width: 90vw;
			.item-details-img 
			{
				min-width: 90vw;
				min-height: 20vh;
			}					
		}

		.item {
			.item-details-info-sec-desc {
				width: 80vw;
			}
			.item-expand {
				.item-details-info-mobile {
					display: block;
					margin: 15% 0;
					.small-icon {
						margin-right: 10px;
						width: 20px;
						height: 20px;
					}
				}
				.item-location{
					.button {
						width: 30vw;
					}
				}
			}
		}
	}
	
	.navbar{
		padding:2% 0;
	}

	.navbarbg {
		width: 100vw;
		height: 7%;
		.nav-item {	
			width: 100%;
			&:nth-child(2) {
				padding: 0 7%;
			}
			label {
				display: none;
			} 
		}
	}
}

}

